@import url(//cdn.jsdelivr.net/npm/keyrune@latest/css/keyrune.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: 5fr 1.5fr;
  grid-auto-rows: auto;
  color: white;
  background-color: #347092;
  background-image:
  -webkit-gradient(linear,  
    left top, right top,  
    color-stop(0, transparent), 
    color-stop(0, #050F17)),
  radial-gradient(
    circle at left,
    #347092,
    #063858,
    #042439,
    #050F17
  );
  background-image:
  linear-gradient(90deg,  
    transparent 0 77%, 
    #050F17 0),
  radial-gradient(
    circle at left,
    #347092,
    #063858,
    #042439,
    #050F17
  );
  height: 100vh;
}

header, footer {
  display: flex;
  align-items: center;
  min-height: 50px;
  grid-column: span 2;
  padding: 0 1rem;
}

h2 {
  font-size: 2rem;
  color: #c8371b;
  font-weight: 900;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: smaller;
  list-style: none;
  align-items: baseline;
}

.footer a {
  font-weight: bold;
  text-decoration: none;
  color: #c8371b;
}

.react-logo {
  display: inline-block;
  max-height: 20px;
  vertical-align: bottom;
  padding: 0 3px;
}

.react {
  color: #61dafb;
}

.filters {
    grid-area: 2 / 2 / span 1;
    background-color: #050F17;
    margin-top: -1rem;
    margin-bottom: -1rem;
}


.filters {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    /* background-color: lightslategray; */
}

.filter-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    list-style: none;
}

.filter-list li {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.filter-set {
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  grid-template-columns: repeat(2, 1fr);
  margin: 1rem 0;
  padding-bottom: 1rem;
  border-top: 1px solid #9BA2A9;
  border-bottom: 1px solid #9BA2A9;
}

.filter-set > label {
  text-align: center;
}

/* HIDE RADIO */
[type="radio"] { 
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* ICON STYLES */
[type="radio"] + i {
  color: #9BA2A9;
  cursor: pointer;
}

[type="radio"] + i:hover {
  text-shadow: -1px -1px 0 #fff,  
  1px -1px 0 #fff,
  -1px 1px 0 #fff,
  1px 1px 0 #fff;
}

/* CHECKED STYLES */
[type="radio"]:checked + i {
  color: #c8371b;
}

.filter-list li {
  line-height: 1.5rem;
  font-weight: bold;
}

.filter-list li input[type="text"] {
    width: 2rem;
    cursor: not-allowed;
} 

input[type="checkbox"] {
    position: relative;
    min-width: 2.5rem;
    height: 1rem;
    background: #bdc3c7;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border-radius: 20px;
    outline: none;
    -webkit-transition: .4s;
    transition: .4s;
    box-shadow: inset 0 0 0.3rem rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }
  
  input[type="checkbox"]:checked {
    background: #c8371b;
  }
  
  input[type="checkbox"]:checked::before {
    left: 1.5rem;
  }
  
  input[type="checkbox"]::before {
    z-index: 2;
    position: absolute;
    content: "";
    left: 0;
    width: 1rem;
    height: 1rem;
    background: #fff;
    border-radius: 50%;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    border-radius: 0 .1rem .3rem rgba(0, 0, 0, 0.2);
    -webkit-transition: .4s;
    transition: .4s;
  }

  /* BUTTON STYLES */
  button {
    text-transform: uppercase;
    font-weight: 800;
    padding: 10px;
    border-radius: 5px;
    border: none;
    display: block;
    margin: 2rem auto;
    cursor: pointer;
  }

  button:hover {
    letter-spacing: 1px;
    border: 3px solid #c8371b;
    -webkit-transition: all 0.2s ease 0s;
    transition: all 0.2s ease 0s;
  }

  button:disabled {
    cursor: progress;
  }
.cards-container {
    display: grid;
    grid-gap: 1rem;
    gap: 1rem;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: minmax(30%, auto);
    min-height: 85vh;
}

.loader {
    grid-area: 1 / 1 / span 3 / span 5;
}
.image.hover:hover {
  border-radius: 4%;
  cursor: pointer;
  -webkit-animation: pulse 2s ease-in-out infinite;
          animation: pulse 2s ease-in-out infinite;
}

.card {
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 900px;
          perspective: 900px;
  max-height: 330px;
}

.card.flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.cardFront, 
.cardBack {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.cardFront {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.image {
  height: 100%;
  border-radius: 4%;
  display: inline-block;
}

@-webkit-keyframes pulse {
  0%, 100% {
    box-shadow:
    0 0 10px 5px rgba(255, 233, 135, 0.2), 
    0 0 20px 10px rgba(239, 193, 106, 0.4); 
  }
  50% {
    box-shadow:
    0 0 40px 20px rgba(220, 161, 72, 0.35),  
    0 0 60px 30px rgba(255, 233, 135, 0.8); 
  }
}

@keyframes pulse {
  0%, 100% {
    box-shadow:
    0 0 10px 5px rgba(255, 233, 135, 0.2), 
    0 0 20px 10px rgba(239, 193, 106, 0.4); 
  }
  50% {
    box-shadow:
    0 0 40px 20px rgba(220, 161, 72, 0.35),  
    0 0 60px 30px rgba(255, 233, 135, 0.8); 
  }
}
  
.loader {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loader-caption {
  margin-top: 5px;
  font-weight: bold;
}

.colour-wheel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 75px;
}

.between {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.evenly {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  margin-top: 7px;
}

[class$="-mana"] {
  width: 20px;
  height: 20px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  display: flex;
}

.w-mana {
  background-color: #ffd;
}

.u-mana {
  background-color: #378bc6;
}

.b-mana {
  background: #000;
}

.r-mana {
  background: #af1d1d;
}

.g-mana {
  background: #5bd387;
}

@-webkit-keyframes scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50%,
  75% {
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
  }
  78%,
  100% {
    opacity: 0;
  }
}

@keyframes scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50%,
  75% {
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
  }
  78%,
  100% {
    opacity: 0;
  }
}

[class$="-mana"]:before {
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 50%;
  opacity: 0.7;
  -webkit-animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
          animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.w-mana:before {
  background-color: #eae0bd;
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms;
}

.u-mana:before {
  background-color: #378bc6;
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms;
}

.b-mana:before {
  background-color: #000;
  -webkit-animation-delay: 600ms;
          animation-delay: 600ms;
}

.r-mana:before {
  background-color: #af1d1d;
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms;
}

.g-mana:before {
  background-color: #5bd387;
  -webkit-animation-delay: 1000ms;
          animation-delay: 1000ms;
}

